@font-face {
    font-family: "Poppins";
    font-weight: 700;
    font-style: normal;
    src: local("Poppins Bold"),
        local("Poppins-Bold"),
        url("./fonts/poppins_700.woff2") format("woff2"), 
        url("./fonts/poppins_700.woff") format("woff"); 
    }
@font-face {
    font-family: "Poppins";
    font-weight: 600;
    font-style: normal;
    src: local("Poppins SemiBold"),
        local("Poppins-SemiBold"),
        url("./fonts/poppins_600.woff2") format("woff2"), 
        url("./fonts/poppins_600.woff") format("woff"); 
    }
@font-face {
    font-family: "Poppins";
    font-weight: 500;
    font-style: normal;
    src: local("Poppins Medium"),
        local("Poppins-Medium"),
        url("./fonts/poppins_500.woff2") format("woff2"), 
        url("./fonts/poppins_500.woff") format("woff"); 
    }
@font-face {
    font-family: "Poppins";
    font-weight: 400;
    font-style: normal;
    src: local("Poppins Regular"),
        local("Poppins-Regular"),
        url("./fonts/poppins_400.woff2") format("woff2"), 
        url("./fonts/poppins_400.woff") format("woff"); 
    }

body {
    background-image: url('background.png');
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: 100% 100vh;
    font-family: 'Poppins', sans-serif;
    position: relative;
}

img {
    max-width: 84px;
    box-shadow: 0px 4px 24px 0px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
}


.app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 464px;
    margin: 10% auto;
}

h1 {
    color: #ffffff;
    text-transform: uppercase;
    font-weight: 700;
    text-align: left;
    font-size: 36px;
    line-height: 54px;
    margin: 0;
    align-self: center;
}

.startBtn {
    border-radius: 12px;
    border: none;
    color: #1D355D;
    padding: 1rem 2rem;
    margin: auto;
    /* margin-left: 90px; */
    align-self: center;
    background-color: #ffffff;
    font-size: 18px;
    font-weight: 600;
    line-height: 27px;
}

.container {
    background-color: #ffffff;
    padding: 2rem;
    padding-top: 3rem;
    border-radius: 24px;
    width: 400px;
    display: flex;
    flex-direction: column;
    position: relative;
}

.container::before {
    content: '';
    width: 150px;
    height: 100px;
    position: absolute;
    top: -10%;
    left: 70%;
    background-image: url('undraw_adventure_4hum.svg');
    background-position: right 100%;
    background-repeat: no-repeat;
    background-size: 150px 100px;
}

@media(max-width: 550px) {
    .container {
        max-width: 224px;
    }

    .container::before {
        left: 55%;
       background-size: 100px 60px;
    }

    h1 {
        align-self: center;
    }
    .startBtn {
        margin-left: 170px;
    }
}

h3 {
    color: #2F527B;
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
}

.answers {
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}

.answers button {
    padding: 1rem;
    padding-left: 82px;
    background-color: transparent;
    border: 1px solid #6066D0;
    color: #6066D0;
    border-radius: 5px;
    width: 100%;
    min-height: 56px;
    cursor: pointer;
    text-align: left;
    font-size: 18px;
    line-height: 27px;
    font-weight: 500;
}

.answers button:first-of-type::before {
    content: 'A';
    width: 2rem;
    height: 2rem;
    left: 55px;
    position: absolute;
}

.answers button:nth-of-type(2)::before {
    content: 'B';
    width: 2rem;
    height: 2rem;
    left: 55px;
    position: absolute;
}

.answers button:nth-of-type(3)::before {
    content: 'C';
    width: 2rem;
    height: 2rem;
    left: 55px;
    position: absolute;
}

.answers button:nth-of-type(4)::before {
    content: 'D';
    width: 2rem;
    height: 2rem;
    left:55px;
    position: absolute;
}

.answers .rightAnswer {
    background-image: url('/check-24px.svg');
    background-position: 95% center;
    background-repeat: no-repeat;
    background-size: 20px;
}

.answers .wrongAnswer {
    background-image: url('/cancel-24px.svg');
    background-position: 95% center;
    background-repeat: no-repeat;
    background-size: 20px;
}

p {
    color: #1D355D;
}

.nextBtn {
    background-color: #F9A826;
    color: #ffffff;
    border: none;
    padding: 16px 36px;
    margin-top: 1rem;
    border-radius: 12px;
    align-self: flex-end;
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
}


.tryAgainBtn {
    max-width: 209px;
    max-height: 62px;
    background-color: transparent;
    color: #1D355D;
    padding: 18px 60px;
    border: 2px solid #1D355D;
    border-radius: 5px;
    font-weight: 600;
    font-size: 18px;
    line-height: 27px;
}

.tryAgainBtn:hover {
    background-color: #F9A826;
    color: #ffffff;
    border: #ffffff;
}

.result {
    background-image: url('/undraw_winners_ao2o.svg');
    background-position: center 15%;
    background-repeat: no-repeat;
    background-size: 238px 128px;
    padding-top: 250px;
    display: flex;
    flex-direction: column;
    /* gap: 2rem; */
    align-items: center;
    justify-content: center;
}

.result::before {
    content: '';
    background-image: none;
}

h2 {
    color: #1D355D;
    font-size: 48px;
    line-height: 72px;  
    font-weight: 700;
    margin: 0;
}

b {
    color: #60BF88;
    font-weight: 800;
    font-size: 40px;
}

.result p {
    font-weight: 400;
    font-size: 26px;
    line-height: 44px;
    margin: 0;
    padding-bottom: 71px;
}

button {
    outline: none;
}